.education {
  background-color: rgba(128, 128, 128, 0.096);
  margin: 50px 0;
  padding-left: 140px;
}
.education hr {
  color: green;
  width: 400px;
  margin: 10px auto;
}

.education h2 {
  font-weight: bold;
  color: rgb(16, 132, 124);
}

.education .vertical-timeline-element-title {
  color: rgb(16, 132, 124);
}

.education .vertical-timeline-element-subtitle {
  color: #1e1e2c;
}

/*--- Mobile-View----*/

@media (max-width: 600px) {
  .education {
    margin: 0;
    padding: 0;
    margin-top: 30px;
  }

  .education h2 {
    padding-top: 20px;
  }

  .education hr {
    width: 280px;
    margin-bottom: 10px;
  }
}
