.project{
   
    margin:50px 0;
    padding-left: 140px;
}
.project hr{
    color:green;
    width:400px;
    margin: 10px auto ;
    }
    
.project h2{
        font-weight: bold;
        color:rgb(16, 132, 124)
    }

 
 .project .bdge{
    position: relative;
    top:8px;
    left:0;
    z-index: 2;
}

.project-text{
    color:white;
}



.view-btn{
    position: relative;
    display: inline-block;
    margin: 15px;
    padding: 10px 40px;
    text-align: center;
    font-size: 18px;
    letter-spacing: 1px;
    text-decoration: none;
    color: rgb(16, 171, 171);
    background: transparent;
    cursor: pointer;
    transition: ease-out 0.5s;
    border: 2px solid rgb(16, 171, 171);
    border-radius: 10px;
    box-shadow: inset 0 0 0 0 rgb(16, 171, 171);
  }
  
  .view-btn:hover {
    color: white;
    box-shadow: inset 0 -100px 0 0 rgb(16, 145, 171);
  }
  
  .view-btn:active {
    transform: scale(0.9);
  }


/*--- Mobile-view----*/

@media (max-width:600px){
  .project{
    margin: 0;
    padding: 0;
    
  }

  #dark .project hr{
    color:white;
    
  }


  .project .forMobile{
    display: flex;
    justify-content: center;
    padding-top: 50px;
    
   

  }

  .project .forMobile .card{
    width:100%;
  }

  .project .bdge{
    position: absolute;
    top:35px;
    left:45px;
    height:20px;
    z-index: 2;
}
}



 


 