.about{
   margin: 40px 0px; 
   padding:20px;
   margin-left: 210px;
   box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;

}

.about-img img{
    border-radius: 10px;
    width:100%;
    height:400px;
    object-fit: cover;

}

.about-content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.about-content h1{
    color:grey;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-bottom: 2px solid grey;
}

.about-content p{
    font-weight: 600;
    text-align: justify;
    padding: 20px;
}


/* DARK MODE   */

#dark .about{
    box-shadow: none;
    background-color: rgba(0,0,0,0.15);

}

#dark .about h1{
    color:white;
}



/*---- Mobile View --- */

@media (max-width:600px){
    .about{
        margin: 0;
        padding: 0;
        margin-top: 20px;
    }
    .about-content h1{
      margin-top: 10px;  
      width:100%;
      border: none;
      text-align: center;
    }
}

