.techstack{
    padding-left: 130px;
    margin-top: 60px;
}

.techstack hr{
color:green;
width:400px;
margin: 10px auto ;
}

.techstack h2{
    font-weight: bold;
    color:rgb(16, 132, 124)
}


.techstack .card{
    cursor: pointer;
   
}


.techstack .project-icon{
   font-size: 2rem; 
   margin-right: 10px;
}
.techstack .card:hover{
background-color: rgb(240, 220, 91);  
color:black;
border: none;
}


.techstack .card2 {
    position: relative;
    width: 210px;
    height: 104px;
    background-color: #000;
    display: flex;
    flex-direction: column;
    justify-content: end;
    padding: 12px;
    gap: 12px;
    color:white;
    border-radius: 8px;
    cursor: pointer;
  }
  
  /* .techstack .card2::before {
    content: '';
    position: absolute;
    inset: 0;
    left: -5px;
    margin: auto;
    width: 210px;
    height: 104px;
    border-radius: 10px;
    background: linear-gradient(-45deg, #e81cff 0%, #40c9ff 100% );
    z-index: -10;
    pointer-events: none;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  } */
  
  .techstack .card2::after {
    content: "";
    z-index: -1;
    position: absolute;
    inset: 0;
    background: linear-gradient(-45deg, #fc00ff 0%, #00dbde 100% );
    transform: translate3d(0, 0, 0) scale(0.5);
    filter: blur(20px);
  }
  

  
 
  
  /* .techstack .card2 p:last-child {
    color: #e81cff;
    font-weight: 600;
  } */
  
 .techstack .card2:hover::after {
    filter: blur(30px);
  }
  
  .techstack .card2:hover::before {
    transform: rotate(-90deg) scaleX(0.34) scaleY(0.77);
  }
  




  /*    DARK MODE     */

