      /*--- Mobile-view-- */

      @media (max-width:600px){
        .techstack{
          margin: 0;
          padding: 0;
          margin-top: 50px;
        }

        .techstack .card{
          width: 95%;
          height: 120px;
          display: flex;
          justify-content: center;
          align-items: center;
        
        }

        #dark .card2{
          width:95%;
        }

        #dark .techstack hr{
          color:white;
          width:280px;
        }

      }
  