.contact {
  padding-left: 130px;
  margin-bottom: 60px;
  padding-top: 50px;
}

.card0 {
  box-shadow: 0px 4px 8px 0px #757575;
  border-radius: 0px;
}

.card3 {
  margin: 0px 40px;
}

.card1 .image {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

/* .border-line{
    border-right: 1px solid #e0e0e0e0;
} */

.line {
  height: 1px;
  width: 45%;
  background-color: #e0e0e0e0;
  margin-top: 10px;
}

.or {
  display: flex;
  width: 10%;
  font-weight: bold;
}

::placeholder {
  color: #bdbdbd;
  opacity: 1;
  font-weight: 300;
}

input,
textarea {
  padding: 10px 12px;
  border: 1px solid lightgray;
  border-radius: 2px;
  margin-top: 5px;
  margin-top: 2px;
  width: 100%;
  color: #138781;
  font-size: 1rem;
  letter-spacing: 2px;
}

input:focus,
textarea:focus {
  box-shadow: none !important;
  outline: 1px solid #138781 !important ;
  outline-width: 0px;
}

.contact-button {
  display: inline-block;
  width: 150px;
  height: 50px;
  border-radius: 10px;
  border: 1px solid #03045e;
  position: relative;
  overflow: hidden;
  transition: all 0.5s ease-in;
  z-index: 1;
}

.contact-button::before,
button::after {
  content: "";
  position: absolute;
  top: 0;
  width: 0;
  height: 100%;
  transform: skew(15deg);
  transition: all 0.5s;
  overflow: hidden;
  z-index: -1;
}

.contact-button::before {
  left: -10px;
  background: #240046;
}

.contact-button::after {
  right: -10px;
  background: #5a189a;
}

.contact-button:hover::before,
button:hover::after {
  width: 58%;
}

.contact-button:hover span {
  color: #e0aaff;
  transition: 0.3s;
}

.contact-button span {
  color: #03045e;
  font-size: 18px;
  transition: all 0.3s ease-in;
}

.contact .card .card3 .or {
  color: black;
}

/* Dark mode  */
#dark .contact .card {
  background-color: #410b29;
}

#dark .contact .card .card3 h6,
.or {
  color: white;
}

/*--- Mobile-View---*/

@media (max-width: 600px) {
  .contact {
    padding: 0;
    margin: 0;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .contact .card {
    width: 89%;
    margin-left: 30px;
  }

  .contact .card .card3 h6 {
    text-align: center;
  }
  #dark .contact .card .card3 h6,
  .or {
    color: white;
  }
}
